import Vue from 'vue'
import Router from 'vue-router'
import welcome from '@/view/admin/admin/welcome'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      meta: {
        title : '首页',
      },
      component: () => import('@/view/admin/admin/index.vue'),
      redirect : 'welcome',
      children : [
          {
            meta: {
              title : '合同管理',
            },
            path: '/admin/task/contract',
            component: () => import('@/view/admin/task/contract.vue')
          },
          {
            meta: {
              title : '添加合同',
            },
            path: '/admin/task/add_contract',
            component: () => import('@/view/admin/task/add_contract.vue')
          },
          {
            meta: {
              title : '余额管理',
            },
            path: '/admin/enterprise/wallet',
            component: () => import('@/view/admin/wallet/list.vue')
          },
          {
            meta: {
              title : '余额充值',
            },
            path: '/admin/enterprise/wallet_up',
            component: () => import('@/view/admin/wallet/wallet_up.vue')
          },
          {
            meta: {
              title : '人才管理',
            },
            path: '/enterprise/bind_user',
            component: () => import('@/view/admin/user/list.vue')
          },
          {
            meta: {
              title : '任务管理',
            },
            path: '/admin/task/list',
            component: () => import('@/view/admin/task/list.vue')
          },
          {
            
            path: '/welcome',
            component: () => import('@/view/admin/admin/welcome.vue'),
            meta: {
              title : '千马平台企业管理平台',
            },
          },
          {
            
            path: '/admin/task/add',
            component: () => import('@/view/admin/task/add.vue'),
            meta: {
              title : '添加任务-千马平台企业管理平台',
            },
          },
          {
            
            path: '/admin/enterprise/certification',
            component: () => import('@/view/admin/certification/index.vue'),
            meta: {
              title : '认证信息-千马平台企业管理平台',
            },
          },
          {
            
            path: '/admin/enterprise/invoice',
            component: () => import('@/view/admin/info/invoice.vue'),
            meta: {
              title : '发票信息-千马平台企业管理平台',
            },
          },
           {
            
            path: '/admin/enterprise/address',
            component: () => import('@/view/admin/info/address.vue'),
            meta: {
              title : '开票地址-千马平台企业管理平台',
            },
          },
      ]
    },
    
    {
      path: '/admin/login',
      component: () => import('@/view/admin/login/index.vue'),
      meta: {
        title : '登陆-千马平台企业管理平台',
      },
    }
  ],
  mode : "history"
})



